import $ from 'jquery';

let callMap = { 
    map : null ,
    draw : function(){
        var mapContainer = document.getElementById('map'), //   div 
        mapOption = {
            center : new kakao.maps.LatLng(36.633535, 127.425882), //  
            level : 4
        //   
        };
      
        callMap.map =new kakao.maps.Map(mapContainer, mapOption);
    },
    init : function(){
        $.getScript('//dapi.kakao.com/v2/maps/sdk.js?appkey=9ced506ecc4ce66b5b7921c530959da9&libraries=services,drawing&autoload=false').done(function(data){
            
            let daumMakers = window.daum.maps;
            daumMakers.load(function(){
                //callMap.draw();
            });
        });
    }, 
    current : function(callback) {
        if(navigator.geolocation){
            navigator.geolocation.getCurrentPosition(function(position) {
              var lat = position.coords.latitude, // 
                lon = position.coords.longitude; // 
               
                var locPosition = new kakao.maps.LatLng(lat, lon), //    geolocation   
                message = '<div style="padding:3px;"> ?!</div>'; //   
                    //   
                callMap.displayMarker(locPosition, message);
                if(callback) {
                    callback( {"lat":lat,"lon":lon} );
                }
            },function(failText){console.log(failText)});   
        }
       
    },displayMarker : function(location, message){
        var map = callMap.map;

        var marker = new kakao.maps.Marker({  
            map: map, 
            position: location
         }); 
 
        var iwContent = message, //   
            iwRemoveable = true;
 
 
        //  
        var infowindow = new kakao.maps.InfoWindow({
            content : iwContent,
            removable : iwRemoveable
        });
     
        //    
        infowindow.open(map, marker);
        map.setCenter(location);  
    }
    ,currentByHeader : function(callback) { //2020.06  ,  
        // -   
        var geocoder = new kakao.maps.services.Geocoder();

        if(navigator.geolocation){
            navigator.geolocation.getCurrentPosition(function(position) {
              var lat = position.coords.latitude, // 
                lon = position.coords.longitude; // 
                
                 geocoder.coord2Address(lon, lat,
                    function(result, status){
                    if(status && status.indexOf('OK')>-1){
                        if(callback)
                        callback( {"lat":lat,"lon":lon,"addressInfo":result});
                    }
                });
            },function(failText){console.log(failText)});   
        }
    }
}



export default callMap




